.form__save-button {
  width: 100%;
  max-width: 358px;
  height: 50px;
  padding: 0;
  background: #000000;
  outline: none;
  border: none;
  border-radius: 2px;
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
  color: #ffffff;
  cursor: pointer;
}

.form__save-button:hover {
  opacity: 0.8;
}

@media screen and (max-width: 468px) {
  .form__save-button {
    font-size: 14px;
    line-height: 17px;
    height: 46px;
  }
}
