.footer__info {
    max-width: 318px;
    min-width: 282px;
    font-family: 'Inter', 'Arial', 'Helvetica', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    color: #545454;
    margin: 0;
    padding: 0;
}

@media screen and (max-width: 500px) {
    .footer__info {
        font-size: 14px;
        line-height: 17px;
    }
}